<template>
  <v-container class="mt-10">
    <div class="authprev">
      <v-card class="pa-3">
        <h3>Login</h3>
        <v-divider class="ma-3"></v-divider>
        <v-form ref="signin">
          <v-col cols="12" sm="12" md="12" class="pa-0 ma-0">
            <v-text-field
              label="username"
              outlined
              dense
              name="username"
              autocomplete="false"
              v-model="username"
              rounded
              prepend-inner-icon="mdi-account-outline"
            ></v-text-field>
          </v-col>
          <v-col cols="12" sm="12" md="12" class="pa-0 ma-0">
            <v-text-field
              label="password"
              outlined
              dense
              rounded
              v-model="password"
              :append-icon="show1 ? 'mdi-eye' : 'mdi-eye-off'"
              :type="show1 ? 'text' : 'password'"
              name="input-10-1"
              hint="At least 8 characters"
              counter
              prepend-inner-icon="mdi-key-outline"
              @click:append="show1 = !show1"
            ></v-text-field>
          </v-col>
          <v-col cols="12" sm="12" md="12" class="pa-0 ma-0 text-center">
            <v-btn
              class="col-md-12"
              dense
              rounded
              color="primary"
              @click="handleSubmit"
              :dark="!loading"
              elevation="3"
              :loading="loading"
            >
              <v-icon>mdi-login-variant</v-icon> login
            </v-btn>
          </v-col>
        </v-form>
        <v-divider class="ma-3"></v-divider>
        <router-link to="/register">
          <p>Create account</p>
        </router-link>
      </v-card>
    </div>
  </v-container>
</template>
<script>
export default {
  data() {
    return {
      show1: false,
      show2: true,
      show3: false,
      show4: false,
      validity: false,
      loading: false,
      username: "",
      password: "",
    };
  },

  methods: {
    successNotification(text) {
      this.$notify({
        group: "success",
        type: "success",
        title: "success",
        text: text,
      });
    },
    errorNotification(text, type = "error") {
      this.$notify({
        group: "errors",
        type: type,
        title: "error",
        text: text,
      });
    },

    handleSubmit() {
      this.loading = true;

      this.$store
        .dispatch("signIn", {
          username_u: this.username,
          password: this.password,
        })
        .then(async (resul) => {
          this.loading = false;

          if (resul.status === 200 && resul.data.code === 200) {
            if (this.$route.query.nextPath) {
              this.$router
                .push({
                  name: this.$route.query.nextPath,
                  params: this.$route.params,
                })
                .then(null, (err) => {
                  this.$notify({
                    group: "errors",
                    type: "success",
                    title: "error",
                    text: "successfully",
                  });
                  this.$router.push({ name: "NotFound" });
                  return err;
                });
            } else this.$router.push({ name: "Home" });
          }
        })
        .catch((err) => {
          this.loading = false;
          this.$notify({
            group: "errors",
            type: "success",
            title: "error",
            text: err.message,
          });
          return err;
        });
    },
  },
};
</script>
